import classes from './PostItem.module.css';
import React from 'react';
import trashIcon from '../components/layout/img/trash-icon.png';

function PostItem(props) {
  const userPerms = props.userPerms;
  console.log('post item userPerms: ', userPerms);

  const isAdmin = userPerms === 'admin';
  console.log(props.author);
  return(
    <div className={classes.container}>
      <div className={classes.titleWrapper}>
        <p>{props.title}</p>
        {isAdmin && (
          <div className={classes.btnWrapper} onClick={(event) => props.handlePostDeletion(event, props.id)}>
            <img className={classes.deleteBtnIcon} src={trashIcon} />
          </div>
        )} 
      </div>
      <div className={classes.imgContainer}>
        <img className={classes.imgStyle} src={props.image} />
      </div>
      <div className={classes.descriptionWrapper}>
        <p>{props.description}</p>
      </div>
      <div className={classes.authorWrapper}>
        <p>{props.author}</p>
      </div>
    </div>
  );
};

export default PostItem;