import firebase from 'firebase/compat/app';
import 'firebase/compat/database';
import { useState, useEffect } from 'react';
import Modal from 'react-modal';
import classes from './MapItem.module.css';
import MarkerModalClasses from './MarkerModal.module.css';
import img from '../images/map-item-img.png';

Modal.setAppElement('#root'); // Set the root element for accessibility

function MapItem() {
  const [markers, setMarkers] = useState([]);
  const [imageDimensions, setImageDimensions] = useState({ width: 0, height: 0 });
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalTitle, setModalTitle] = useState('');
  const [modalCoordinates, setModalCoordinates] = useState({ x: 0, y: 0 });

  const fetchMarkers = () => {
    const markersRef = firebase.database().ref('/markers');

    markersRef.once('value').then((snapshot) => {
      const data = snapshot.val();
      if (data) {
        setMarkers(Object.values(data));
      }
    });
  };

  useEffect(() => {
    const handleImageClick = (event) => {
      const { clientX, clientY } = event;

      console.log('Clicked at:', { clientX, clientY });

      const { left, top } = image.getBoundingClientRect();

      const x = (clientX - left) / imageDimensions.width;
      const y = (clientY - top) / imageDimensions.height;

      if (!Number.isFinite(x) || x < 0 || x > 1 || !Number.isFinite(y) || y < 0 || y > 1) {
        console.error('Invalid coordinates:', x, y);
        return;
      }

      setModalCoordinates({ x, y });
      setIsModalOpen(true);
    };

    const image = document.getElementById('main-map');

    const handleImageLoad = () => {
      setImageDimensions({
        width: image.width,
        height: image.height,
      });

      image.addEventListener('click', handleImageClick);
    };

    if (image.complete) {
      handleImageLoad();
    } else {
      image.addEventListener('load', handleImageLoad);
    }

    fetchMarkers();

    return () => {
      image.removeEventListener('click', handleImageClick);
    };
  }, [imageDimensions.width, imageDimensions.height]);

  const closeModal = () => {
    setIsModalOpen(false);
    setModalTitle('');
  };

  const saveMarker = () => {
    const title = modalTitle.trim();

    if (title) {
      const markersRef = firebase.database().ref('/markers');

      markersRef.push({ x: modalCoordinates.x, y: modalCoordinates.y, title }, (error) => {
        if (error) {
          console.error('Error adding marker:', error);
        } else {
          fetchMarkers(); // Update markers after saving
        }
      });

      closeModal();
    }
  };

  return (
    <div className={classes.contentContainer}>
      <div style={{ position: 'relative' }}>
        <img src={img} id="main-map" style={{ maxWidth: '100%' }} />

        {markers.map((marker, index) => (
          <div className={classes.marker}
            key={index}
            style={{
              position: 'absolute',
              left: marker.x * imageDimensions.width + 'px',
              top: marker.y * imageDimensions.height + 'px',
              transform: 'translate(-50%, -50%)',
            }}
            title={marker.title}
          />
        ))}

        <Modal 
          isOpen={isModalOpen}
          onRequestClose={closeModal}
          contentLabel="Add Marker Modal"
          className={MarkerModalClasses.modalContainer}
          overlayClassName={MarkerModalClasses.overlay}
        >
          <h2>Pridti ymekl</h2>
          <label htmlFor="markerTitle">Pavadinimas:</label>
          <input
            type="text"
            id="markerTitle"
            value={modalTitle}
            onChange={(e) => setModalTitle(e.target.value)}
          />
          <button onClick={saveMarker}>Isaugoti</button>
          <button onClick={closeModal}>Ataukti</button>
          
        </Modal>
      </div>
    </div>
  );
}

export default MapItem;