import { Link } from 'react-router-dom';
import classes from './Posts.module.css';

import lapuociaiImg from '../images/lapuociai-icon.jpg';
import spygliuociaiImg from '../images/spygliuociai-icon.jpg';

function PostsPage() {
  return(
    <div>
      <div className={classes.titleContainer}>
        <h1>Pasirinkite kategorij</h1>
      </div>
      <div className={classes.cardContainer}>
        <div className={classes.card}>
          <Link link to='/deciduous'>
            <div className={classes.container}>
              <img className={classes.img} src={lapuociaiImg} />
              <div className={classes.textOverlay}>Lapuoiai</div>
            </div>
          </Link>
        </div>
        <div className={classes.card}>
          <Link  link to='/coniferous'>
            <div className={classes.container}>
              <img className={classes.img} src={spygliuociaiImg} />
              <div className={classes.textOverlay}>Spygliuoiai</div>
            </div>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default PostsPage;