import { useRef } from "react";

import { useState } from 'react';
import { storage } from '../firebase';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import {v4} from 'uuid';
import classes from './NewPostForm.module.css';
import MapItem from "./MapItem";

function NewPostForm(props) {
  const [imageUpload, setImageUpload] = useState(null);

  const [uploading, setUploading] = useState(false);

  const titleInputRef = useRef();
  const descriptionInputRef = useRef();
  const categoryInputRef = useRef();
  const authorInputRef = useRef();

  const submitHandler = async (event) => {
    event.preventDefault();
    let url = '';
    if (imageUpload == null) return;
    setUploading(true);
    const imageRef = ref(storage, `images/${imageUpload.name + v4()}`);
    try {
      await uploadBytes(imageRef, imageUpload);
    url = await getDownloadURL(imageRef);
    console.log('download url:', url);
    alert('Image uploaded successfully');
    } finally {
      setUploading(false);
    }

    if (uploading) {
      await new Promise(resolve => setTimeout(resolve, 100));
    }

    const enteredTitle = titleInputRef.current.value;
    const enteredDescription = descriptionInputRef.current.value;
    const enteredCategory = categoryInputRef.current.value;
    const enteredAuthor = authorInputRef.current.value;

    const postData = {
      title: enteredTitle,
      image: url,
      description: enteredDescription,
      author: enteredAuthor,
      category: enteredCategory,
    };

    props.onAddPost(postData);
  };

  return(
    <div className={classes.formWrapper}>
      <form onSubmit={submitHandler}>

        <div className={classes.divElement}>
          <label className={classes.label} htmlFor='title'>Pavadinimas:</label>
          <input className={classes.textField} type='text' required id='title' ref={titleInputRef} />
        </div>

        <div className={classes.divElement}>
          <label className={classes.label}>Nuotrauka:</label>
          <input className={classes.fileInput} type='file' required id='image' onChange={(event) => {setImageUpload(event.target.files[0])}} />
        </div>

        <div className={classes.divElement}>
          <label className={classes.label}>Apraymas:</label>
          <input className={classes.textField} type='text' required id='description' ref={descriptionInputRef} />
        </div>

        <div className={classes.divElement}>
          <label className={classes.label} htmlFor='author'>Nuotraukos Autorius:</label>
          <input className={classes.textField} type='text' required id='author' ref={authorInputRef} />
        </div>

        <div className={classes.divElement}>
          <label className={classes.label}>Kategorija: </label>
          <select className={classes.dropdown} required id='category' ref={categoryInputRef}>
            <option value='deciduous'>Lapuoiai</option>
            <option value='coniferous'>Spygliuoiai</option>
          </select>
        </div>

        <div className={classes.btnWrapper}>
          <button className={classes.btn}>kelti</button>
        </div>
      </form>
      <div className={classes.mapWrapper}>
      <MapItem />
      </div>
    </div>
    
  );
};

export default NewPostForm;