import { Route, useNavigate } from 'react-router-dom';
import { Routes } from 'react-router-dom';

import { useAuth0 } from '@auth0/auth0-react';
import { useEffect, useState } from 'react';
import firebase from './firebase.js';

import Layout from './components/layout/Layout';
import MainPage from './pages/Main';
import NewPostPage from './pages/NewPost';
import PostsPage from './pages/Posts';
import DeciduousCategPage from './pages/DeciduousCateg';
import ConiferousCategPage from './pages/ConiferousCateg';

function App() {

  const { isAuthenticated, user, isLoading, loginWithRedirect } = useAuth0();
  const [loading, setLoading] = useState(true);
  const [userPermissions, setUserPermissions] = useState(null);

  useEffect(() => {
    const redirectUser = async () => {
      if (!isAuthenticated && !isLoading) {
        // Only redirect if the user is not authenticated and the Auth0 SDK has finished loading
        await loginWithRedirect();
      } else {
        setLoading(false);
      }
    };

    redirectUser();
  }, [isAuthenticated, isLoading, loginWithRedirect]);

  useEffect(() => {
    const storeUserDataInFirebase = async () => {
      if (isAuthenticated && user) {
        const userId = user.sub;
        const userRef = firebase.database().ref(`users/${userId}`);
  
        // Check if the user already exists in the database
        userRef.once('value', (snapshot) => {
          const userData = snapshot.val();
  
          if (!userData) {
            // If the user doesn't exist, set default permissions
            userRef.set({
              name: user.name,
              email: user.email,
              perms: 'guest',
              // Add other custom fields as needed
            });
          }
        });
  
        // Fetch user data again to get the updated permissions
        userRef.once('value', (snapshot) => {
          const userData = snapshot.val();
          setUserPermissions(userData ? userData.perms : null);
          console.log('userData: ', userData, userData.perms);
        });
      }
    };
  
    storeUserDataInFirebase();
    
  }, [isAuthenticated, user, userPermissions]);

  if (loading || isLoading) {
    // Handle the case where the authentication status is being checked or the Auth0 SDK is loading
    return(
      <div>
        authentication status is being checked
      </div>
    ); // or a loading indicator
  }

  const userPerms = userPermissions;
  return (
    <Layout userPerms={userPerms}>
      <Routes>
        <Route path='/react-site-preview' element={<MainPage />} />
        <Route path='/new-post' element={<NewPostPage />} />
        <Route path='/posts' element={<PostsPage />} />
        <Route path='/deciduous' element={<DeciduousCategPage userPerms={userPerms} />} />
        <Route path='/coniferous' element={<ConiferousCategPage userPerms={userPerms} />} />
      </Routes>
    </Layout>
  );
}

export default App;
