import { useState, useEffect } from 'react';
import classes from './Categ.module.css';
import PostList from '../components/PostList';

function DeciduousCategPage(props) {
  const userPerms = props.userPerms;

  const [isLoading, setIsLoading] = useState(true);
  const [loadedPosts, setLoadedPosts] = useState([]);

  const [posts, setPosts] = useState([]);

  useEffect(() => {
    setIsLoading(true);

    fetch('https://react-meetup-demo-a3872-default-rtdb.firebaseio.com/posts-deciduous.json')
    .then(response => {
      return response.json();
    }).then(data => {
      const posts = [];
      
      for (const key in data) {
        const post = {
          id: key,
          ...data[key]
        };

        posts.push(post);
      }

      setIsLoading(false);
      setLoadedPosts(posts);
    });
  }, []);

  if (isLoading) {
    return(
      <section>
        <p>Kraunama...</p>
      </section>
    );
  };

  return(
    <div>
      <h1 className={classes.contentHeader}>Lapuoiai</h1>
      <PostList posts={loadedPosts} setPosts={setPosts} userPerms={userPerms} />
    </div>
  );
};

export default DeciduousCategPage;