import { useState } from 'react';

import classes from './MapBubble.module.css';
import img from './img/map-icon.png';

import UserMapItem from '../UserMapItem';
import Backdrop from './Backdrop';

function MapBubble() {
  const [mapIsOpen, setMapIsOpen] = useState(false);

  function openMapHandler(e) {
    // Check if the click target is the MapItem
    if (e.target.closest(`.${classes.ref}`)) {
      return;
    } 

    setMapIsOpen((prevMapIsOpen) => !prevMapIsOpen);
    console.log('map opened');
  }

  function closeMapHandler() {
    setMapIsOpen(false);
    console.log('map closed');
  };

  return(
    <div className={classes.mapBtn}>
      <div onClick={openMapHandler} className={classes.clickableArea}>
        <img className={classes.img} src={img} />
      </div>
      { mapIsOpen && <Backdrop onClick={closeMapHandler} /> }
      { mapIsOpen && <UserMapItem className={classes.ref} /> }
    </div>
  );
};

export default MapBubble;