import React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';

import classes from '../layout/Navigation.module.css';

import gearImg from './img/gear-icon.png';
import mintiesLogo from './img/minties-logo-img.png';
import uploadIcon from './img/upload-icon.png';

import LogoutButton from '../LogoutButton';

function Navigation(props) {
  const userPerms = props.userPerms;
  const isAdmin = userPerms === 'admin';

  const [isDropdownVisible, setDropdownVisibility] = useState(false);

  const toggleDropdown = () => {
    setDropdownVisibility((prevState) => ! prevState);
  };

  const handleOptionClick = () => {
    setDropdownVisibility(false);
  };

  return(
    <header className={classes.header}>

      <div className={classes.logoContainer}>
        <img className={classes.logo} src={mintiesLogo} />
      </div>

      <div className={classes.container}>
        <div>
          <Link className={classes.btn} link to="/react-site-preview">Pagrindinis</Link>
        </div>
        <div>
          <Link className={classes.btn} link to="/posts">Turinys</Link>
        </div>
        {isAdmin && (
          <div>
            <Link className={classes.uploadBtn} link to="/new-post">
              <img className={classes.uploadIcon} src={uploadIcon} />
            </Link>
          </div>
        )}
      </div>
      
      <div className={classes.cogContainer} onClick={toggleDropdown}>
        <img className={classes.cogIcon} src={gearImg} />
        {isDropdownVisible && (
          <div className={classes.dropdownContainer}>
            <ul className={classes.dropdown}>
              <li><LogoutButton/></li>
            </ul>
          </div>
      )}
      </div>

    </header>
  );
};

export default Navigation;