import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import classes from './PostList.module.css';
import { useLocation } from 'react-router-dom';

import firebase from 'firebase/compat/app';
import 'firebase/compat/database';

import PostItem from "./PostItem.js";

function PostList(props) {
  const userPerms = props.userPerms;
  const postsRef = firebase.database().ref('posts');

  const navigate = useNavigate();
  const location = useLocation();

  // Define fetchData outside useEffect
  const fetchData = () => {
    postsRef.on('value', (snapshot) => {
      const postsData = snapshot.val();
      if (postsData) {
        const postsArray = Object.keys(postsData).map((key) => ({
          id: key,
          ...postsData[key],
        }));
        props.setPosts(postsArray);
      }
    });
  };

  useEffect(() => {
    fetchData();
  }, [props.posts]);

  function handleNavigation(e, pathname) {
    e.preventDefault();
    navigate(pathname, { replace: true });
  };

  const handlePostDeletion = async (event, postId) => {
    event.preventDefault();

    const deciduousRef = firebase.database().ref(`posts-deciduous/${postId}`);
    const coniferousRef = firebase.database().ref(`posts-coniferous/${postId}`);

    try {
      await Promise.all([deciduousRef.remove(), coniferousRef.remove()]);
      console.log('Post deleted successfully');

      // Refresh the page after successful deletion
      window.location.reload();
      handleNavigation();
    } catch (error) {
      console.error('Error deleting post:', error);
    }
  };

  return (
    <div className={classes.postlistContainer}>
      <ul className={classes.postList}>
        {props.posts.map((post) => (
          <PostItem
            key={post.id}
            id={post.id}
            image={post.image}
            description={post.description}
            title={post.title}
            author={post.author}
            
            handlePostDeletion={handlePostDeletion}
            userPerms={userPerms}
          />
        ))}
      </ul>
    </div>
  );
};

export default PostList;