import Navigation from './Navigation';

import MapBubble from './MapBubble';

function Layout(props) {
  const userPerms = props.userPerms;
  console.log(userPerms);
  return(
    <div>
      <Navigation userPerms={userPerms} />
        <main>
          {props.children}
        </main>
      <MapBubble />
    </div>
  );
}

export default Layout;