import { useNavigate } from "react-router-dom";
import classes from './NewPost.module.css';
import NewPostForm from "../components/NewPostForm";

function NewPostPage() {
  const navigate = useNavigate();
  function addPostHandler(postData) {

    if(postData.category === 'coniferous') {

      fetch('https://react-meetup-demo-a3872-default-rtdb.firebaseio.com/posts-coniferous.json', {
      method: 'POST',
      body: JSON.stringify(postData),
      headers: {
        'Content-Type': 'application/json'
      }
    }
  ).then(() => {
    //change navigate when switching between dev and prod
    navigate('/demo-augalija', {replace: true});
  });  
    } else {
      fetch('https://react-meetup-demo-a3872-default-rtdb.firebaseio.com/posts-deciduous.json', {
      method: 'POST',
      body: JSON.stringify(postData),
      headers: {
        'Content-Type': 'application/json'
      }
    }
  ).then(() => {
    //change navigate when switching between dev and prod
    navigate('/demo-augalija', {replace: true});
  }); 
    }
  };

  return(
    <section className={classes.section}>
      <h1 className={classes.header}>kelti turin</h1>
      <NewPostForm onAddPost={addPostHandler} />
    </section>
  );
};

export default NewPostPage;